.topnav {
  position: fixed;
  width: 900px;
  z-index: 10;
  top: 0;
}

.white-shadow{
  text-shadow: -1px 0 white, 0 1px white, 1px 0 white, 0 -1px white
}

.nav-white{
  background-color: white;
}

.topnav a {
  float: left;
  color: black;
  text-align: center;
  vertical-align: middle;
  padding: 14px 16px;
  text-decoration: none;
  font-size: 1.17em;
  font-weight: 700;
  height: 100%;
  display: table-cell;
}

.barsMenu{
  float: right;
  height: 32px;
  padding: 6px;
  margin-top: 8px;
  display: none;
  background: none;
  border-radius: 5px;
}

.barsBtn{
  display: block;
  width: 20px;
  height: 2px;
  background: black;
  position: relative;
  border:1px solid black;
  margin-top: 2px;
}
.leftMenuMobile{
  display: none;
}
.rightMenuMobile{
  display: none;
}
@media(max-width: 992px){
  .topnav{
    width: 100%;
  }
  .barsMenu{
    display:inline-block;
    border:1px solid black; 
    margin-top:20px; 
    margin-right:10vw;  
  }
  .leftMenu{
    display: none;
  }
  .rightMenu{
    display: none;
  }
  .leftMenuMobile{
    display: table-cell;
  }
  .rightMenuMobile{
    display:table-cell;
  }
 
}