body {
  margin: 0;
  padding: 0;
}
h1, h2, h3, h4, h5, h6, .topnav div
{
	font-family: 'Montserrat', 'Roboto', Arial, sans-serif;
}

div
{
	font-family: 'Roboto', Arial, sans-serif;
}

.topnav
{
	-webkit-transition: all 0.3s ease;
			transition: all 0.3s ease;
	padding: 5px 0px;
}

.topnav a
{
	height: inherit;
	margin: auto 0px;
}

.nav-white
{
	box-shadow: 0 6px 10px -4px rgba(0, 0, 0, 0.15); 
	-webkit-transition: all 0.3s ease;
			transition: all 0.3s ease;
}

.leftMenu
{
	font-size: 9.5pt;
	display: flex;
}

.content {
  max-width: 900px;
  margin: auto;
  line-height: normal;
}
.h1 {
  font-size: 48px;
}
.h2 {
  font-size: 36px;
}
.h3 {
  font-size: 24px;
}
.h4 {
  font-size: 18px;
}
.center {
  text-align: center;
  margin: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.left {
  text-align: left;
  margin: auto;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.right {
  text-align: right;
  margin: auto;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.card {
  border-radius: 20px;
  box-shadow: 5px 5px 5px rgba(0,0,0,0.15);
}
.solid-btn,
.trans-btn,
.fit-btn {
  min-height: 45px;
  border-radius: 10px; 
  padding: 10px;
  font-size: 16px; 
  font-weight: bold; 
}
.solid-btn:hover,
.trans-btn:hover,
.fit-btn:hover{
  opacity: 0.8;
}

.solid-btn {
  min-width: 120px;
}
.trans-btn {
  min-width: 120px;
  border: 2px solid;
}
.fit-btn {
  width: fit-content;
}
.white {
  border-color: white;
  color: white;
}
.black {
  border-color: black;
  color: black;
}
.bg-img {
  background-image: url(26bda57eeb02dec8230b9d455add929d.jpg);
  height: 500px;
  background-position: left;
  background-repeat: no-repeat;
  background-size: cover;
}

.bg2-img {
  background-image: url(182d3708c15c1e0d27eeb6bd219e8ecb.jpg);
  height: 500px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
}

.bg3-img {
  background-image: url(2378831fa967f3113eb481edf77426b3.jpg);
  height: 400px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
}
.bg-home-img {
  background-image: url(2ce14a48cc3acd407611619e9814f78d.jpg);
  height: 300px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
}
.mail-img {
  background-image: url(1f618d79949d0754c6c53430563d39db.jpg);
  height: 100px;
  width: 100%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  margin-bottom: -15px;
}
.custom-position{
  margin: 200px 80px 80px 80px
}
.logo-title
{
	display: inline-block;
	width: 80px;
	overflow-wrap: break-word;
	font-size: 16px;
	font-weight: normal;
	margin: auto 0px;
}

.footer
{
	background: whitesmoke;
	font-family: 'Montserrat', 'Roboto', Arial, sans-serif;
}

.card-shadow
{
	border-radius: 20px;
	box-shadow: rgba(0, 0, 0, 0.15) 0px 5px 20px;
}

.white-shadow
{
	text-shadow: 0px 0px 4px white, 0px 0px 4px white, 0px 0px 4px white;
}

/* use this to invert black img to white */
.invert
{
	filter: invert(1);
}

.warm-orange-gradient
{
	background: rgb(247,147,30);
	background: -moz-linear-gradient(127deg, rgba(247,147,30,1) 0%, rgba(242,90,34,1) 41%, rgba(236,9,40,1) 100%);
	background: -webkit-linear-gradient(127deg, rgba(247,147,30,1) 0%, rgba(242,90,34,1) 41%, rgba(236,9,40,1) 100%);
	background: linear-gradient(127deg, rgba(247,147,30,1) 0%, rgba(242,90,34,1) 41%, rgba(236,9,40,1) 100%);
	filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#f7931e",endColorstr="#ec0928",GradientType=1);
}

.dark-to-light
{
	background: rgb(255,255,255);
	background: -moz-linear-gradient(180deg, rgba(255,255,255,1) 15%, rgba(0,0,0,1) 95%, rgba(255,217,67,1) 100%);
	background: -webkit-linear-gradient(180deg, rgba(255,255,255,1) 15%, rgba(0,0,0,1) 95%, rgba(255,217,67,1) 100%);
	background: linear-gradient(180deg, rgba(255,255,255,1) 15%, rgba(0,0,0,1) 95%, rgba(255,217,67,1) 100%);
	filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#ffffff",endColorstr="#ffd943",GradientType=1);
}

.bg-img-sub {
  background-image: url(78e018f59d071321566b16801148d5d4.jpg);
  height: 250px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
}
.bg-detail-sub{
  padding-top:180px;
  text-align: center;
}

.baner-sub-nav{
  display: inline-block;
  margin:0 7px;   
}

.bw-w90{
  word-wrap: normal;
  width: auto;
}

.bw-w80{
  word-wrap: normal;
  width: auto;
}

.overlay {
  /* Height & width depends on how you want to reveal the overlay (see JS below) */   
  height: 100%;
  width: 100%;
  position: fixed; /* Stay in place */
  z-index: 101; /* Sit on top */
  left: 0;
  top: 0;
  background-color: rgb(0,0,0); /* Black fallback color */
  background-color: rgba(0,0,0, 0.9); /* Black w/opacity */
  overflow-x: hidden; /* Disable horizontal scroll */
  transition: 0.5s; /* 0.5 second transition effect to slide in or slide down the overlay (height or width, depending on reveal) */
  display: flex;
  justify-content: center;
  text-align: center; /* Centered text/links */

}
.overlay-content {
  /* position: relative; */

  width: 80%; /* 100% width */
  min-width: 300px;
  background:white;
  height: auto;
  
}
@media screen and (max-width: 992px) {
  .bg-home-img {
    height: 500px;
    }
  .bw-w90{
    word-wrap: break-word;
    width: 90px;
  }
  
  .bw-w80{
    word-wrap: break-word;
    width: 80px;
  }
  .custom-position{
    margin: 300px 10px 10px 10px
  }
  .bg2-img {
    background-position: right;
  }
  .bg-img {
    background-image: url(2b1c42305aee7c83ee0699c4ae31bb63.jpg);
    min-height: 400px;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;
  }
.h1 {
  font-size: 40px;
}
.h2 {
  font-size: 30px;
}
.h3 {
  font-size: 16px;
}
.h4 {
  font-size: 14px;
}

.right {
  text-align: center;
  margin: auto;
  display: flex;
  flex-direction: column;
  align-items: center
}
.leftMenu {
  display: none;
}
  
}