
.img1-img {
  height: auto;
  width: 100%;
}
.img2-img {
  background-image: url(b62b5040c8a6190009540d5c04030ae3.JPG);
  height: 100px;
  width: 100%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  transform: scale(0.6);
}
.img3-img {
  height: 400px;
  width: 100%;
}

.img4-img { 
  height: 250px;
  width: 800px;
}

.payment-detial-popup-img{
  width:40vw;
  height:auto;
}
#carousel {
  width: 100%;
  overflow-x: scroll;
  background-color: white;
  white-space:nowrap;
  -ms-overflow-style: none;
}
#carousel::-webkit-scrollbar {
  display: none;
}

#carousel .slide {
  display: inline-block;
}
.carousel-home{
  display:none;
}
.carousel-img-home{
  display: none;
}


.collab-image-middle{
  display: flex;
  justify-content: center;
  align-items: center 
}


.support-image{
  padding-top: 10px;
  width: 80px;
  height: auto;
}
.tech-image{
  padding-top: 10px;
  width: 70px;
  height: auto;
}
.tech-image-2{
  width: 80px;
  height: auto;
}

.media-image{
  width: 80px;
  height: auto;
}
.production-image{
  width: 80px;
  height: auto;
  max-height: 80px;
  padding-top: 10px;

}
.production-image-2{
  width: 80px;
  height: auto;
  max-height: 80px;
  padding-top: 10px;
}
.komikus-image{
  width: 80px;
  height: auto;
  padding-top: 10px;
}

.bg-orange{
  background: rgb(247,147,30);
	background: -moz-linear-gradient(127deg, rgba(247,147,30,1) 0%, rgba(242,90,34,1) 41%, rgba(236,9,40,1) 100%);
	background: -webkit-linear-gradient(127deg, rgba(247,147,30,1) 0%, rgba(242,90,34,1) 41%, rgba(236,9,40,1) 100%);
	background: linear-gradient(127deg, rgba(247,147,30,1) 0%, rgba(242,90,34,1) 41%, rgba(236,9,40,1) 100%);
	filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#f7931e",endColorstr="#ec0928",GradientType=1);
}

.font-bulb-rice{
  font-size: 20px;
}
.bulb-rice-img{
  min-width: 135px;
}

.bg-slider-home {
  background-image: url(aa11153485fef7a41a62379a29f22816.png);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
}
@media (max-width: 992px) {
  .font-bulb-rice{
    font-size: 14px;
  }
 
  .payment-detial-popup-img{
    width: 80vw;
    height:auto;
  }
  .img1-img {
    display: none;
  }
  .carousel-home{
    display: block;   
  }
  .carousel-home-slider{
    width: 200px;
    margin: 0px auto ; 
  }
  .carousel-img-home{
    height: 250px;
  }
  .img3-img{
    min-height: 150px;
    height: auto;
    min-width: 280px;
    overflow-x: scroll;
  }
  .img4-img {
    height: auto;
    width:100%;
    min-width: 300px;
  }

  .icon-home-responsive{
    text-align: center;
  }

  .bulb-rice-img{
    min-width: 90px;
  }
}