.container {
  background: rgb(0, 0, 0); /* Fallback color */
  background: rgba(0, 0, 0, 0); /* Black background with 0.5 opacity */
  color: black;
  width: 100%;
  max-width: 900px;
  z-index: 100;
  overflow: hidden;
  margin: 0 auto;
  float: none;
  padding: 25px 50px 25px 50px;
  font-family: roboto, sans-serif;
}

.container-full {
  padding: 0;
}
.container .content {
  top: 0;
  background: rgb(0, 0, 0); /* Fallback color */
  background: rgba(0, 0, 0, 0); /* Black background with 0.5 opacity */
  color: black;
  width: 100%;
}

@media screen and (max-width: 992px) {
  .container {  
    padding:25px 15px; 
  }
}